.slider{display: flex;align-items: center;justify-content: center;}
.n_btn{position: absolute;right: 20px;top: 50%;}
#result{float: left; margin-right: 5px;}
.n_btn {background-color: #1c87c9;-webkit-border-radius: 60px;border-radius: 60px;border: none;color: #eeeeee;cursor: pointer;display: inline-block;font-family: sans-serif;font-size: 20px;padding: 5px 15px;text-align: center;text-decoration: none;}
    @keyframes glowing {
      0% {
        background-color: hotpink;
        box-shadow: 0 0 5px hotpink;
      }
      50% {
        background-color: purple;
        box-shadow: 0 0 20px purple;
      }
      100% {
        background-color: hotpink;
        box-shadow: 0 0 5px hotpink;
      }
    }
    .n_btn {
      animation: glowing 1300ms infinite;
    }